/* Bu dosya, tüm sayfalarda ortak olan temel değişkenleri ve stilleri içerir. */
:root {
    --primary-color: #4CAF50; /* Yeşil */
    --secondary-color: #FF9800; /* Turuncu */
    --passed-color: #FF9800; /* Pas geçme için de Turuncu */
    --background-color: #f0f2f5;
    --card-background: #ffffff;
    --text-color: #333;
    --light-text-color: #666;
    --white-color: #fff;
    --border-color: #e0e0e0;
    --shadow-light: rgba(0, 0, 0, 0.1);
    --shadow-dark: rgba(0, 0, 0, 0.2);
    --invalid-color: #ff4d4d;
    --valid-color: #4CAF50;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Nunito', sans-serif;
}

body {
    background-color: var(--background-color);
    color: var(--text-color);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    min-height: 100vh;
}
